import React, {PureComponent} from 'react';
import cx from 'classnames'
import styles from './styles.module.sass'
import Media from 'react-media';

class Price extends PureComponent {
  render() {
    const renderItem = (className, title, lines) => {
      return (
        <div className={cx(className)}>
          <h1 className={styles.title}>{title.map(a =>
            <Media query="(min-width: 820px)">
              {m => m ?
                a
                :
                <div>{a}</div>
              }
            </Media>)}</h1>
          {lines && lines.map(item => (
            <div className={styles.text}>{item}</div>
          ))}
        </div>
      );
    };


    const infoItems = [
      renderItem(cx(styles.cell), [', '], ['4 - 600', '3 - 1000', '2 - 1400']),
      renderItem(styles.cell, [' '], ['3040 - 2500', '4050 - 3500', '5060 - 4500']),
      renderItem(cx(styles.cell), [' '], ['4 - 700', '3 - 1100', '2 - 1500']),
      renderItem(styles.cell, [' ', '  '], ['3040 - 1500', '4050 - 1600', '5060 - 1700', '6080 - 1800', '7080 - 1900'])
    ];

    return (
      <div className={styles.component}>
        <Media query="(min-width: 820px)">
          {m => m
            ?
            <div className={styles.table}>
              <div className={styles.row}>
                {infoItems[0]}
                {infoItems[1]}
              </div>
              <div className={styles.row}>
                {infoItems[2]}
                {infoItems[3]}
              </div>
            </div>
            :
            <div className={styles.tableMin}>
              {infoItems[0]}
              {infoItems[1]}
              {infoItems[2]}
              {infoItems[3]}
            </div>
          }
        </Media>
        <div className={styles.info}>
          <h1 className={styles.title}>    ?</h1>
          <div className={styles.text}>   .     .</div>
          <ul className={styles.ul}>
            <li className={styles.text}>            
                  .
            </li>
            <li className={styles.text}>     ,   
                 .
            </li>
            <li className={styles.text}>      450 . ( 
                 )
            </li>
            <li className={styles.text}>    .</li>
          </ul>
          <div className={styles.text}>         .
          </div>
          <div className={styles.text}>    .</div>
        </div>
      </div>
    );
  }
}

export default Price;


